
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Config from '../config'
function showError(error) {
var innerHTML;
switch (error.code) {
case error.PERMISSION_DENIED:
console.log("")
break;
case error.POSITION_UNAVAILABLE:
console.log("")
break;
case error.TIMEOUT:
console.log("")
break;
case error.UNKNOWN_ERROR:
console.log("")
break;
}
}
export default {
components: {
XHeader,
XButton,
GroupTitle,
Group,
Cell,
CellBox,
XInput,
Divider,
Card,
Flexbox,
FlexboxItem,
},
data() {
return {




courseInfo: {
course: {
name: '',
course_place: '',
course_speaker: ''
},
course_time: {
start: '',
end: ''
}
},
inputArr: [{
icon: 'icon-gonghao',
key: '',
value: ''
}, {
icon: 'icon-xingming',
key: '',
value: ''
}, {
icon: 'icon-bumen',
key: '',
value: ''
}, {
icon: 'icon-didian',
key: '',
value: ''
}
],
value: ''
}
},
mounted() {
var url = `${Config.service.getCourseInfoUrl}/?cid=${this.$route.params.id}`;
console.log(url)
this.$http.get(url).then(response => {

console.log(response.data)
if (response.data.status) {
this.courseInfo = response.data.data
} else {

this.$vux.toast.show({
text: response.data.msg,
type: 'warn'
})
}
}).then(this.getLocation())
},
methods: {
signHandler: function () {
var querystring = require('querystring');
var url = Config.service.couseSignUrl,
signdata = {
course_id: this.courseInfo.course.course_id,
user_id: this.inputArr[0].value,
user_name: this.inputArr[1].value,
user_department: this.inputArr[2].value,
sign_place: this.inputArr[3].value,
};
this.$http.post(url, querystring.stringify(signdata)).then(res => {
console.log(res.data)
this.$vux.alert.show({
title: '',
content: res.data.msg,
onShow() {
console.log('Plugin: I\'m showing')
},
onHide() {
console.log('Plugin: I\'m hiding now')
}
})
}, err => {

});
},
getAddress: function (position) {
console.log("dingwei")
var address,
_this = this,
latitude = position.coords.latitude,
longitude = position.coords.longitude,
lnglatXY=[longitude,latitude];

var geocoder = new AMap.Geocoder({
radius: 1000,
extensions: "all"
});
geocoder.getAddress(lnglatXY, function(status, result) {
if (status === 'complete' && result.info === 'OK') {
_this.inputArr[3].value=result.regeocode.formattedAddress; //
}
});
},
getLocation: function () {
var _this = this
if (navigator.geolocation) {
navigator.geolocation.getCurrentPosition(_this.getAddress, _this.showError);
}
else {
console.log("");
}
},
showError: function (error) {
var innerHTML;
switch (error.code) {
case error.PERMISSION_DENIED:
this.$vux.toast.show({
text: "",
type: 'warn'
})
break;
case error.POSITION_UNAVAILABLE:
this.$vux.toast.show({
text: "",
type: 'warn'
})

break;
case error.TIMEOUT:
this.$vux.toast.show({
text: "",
type: 'warn'
})

break;
case error.UNKNOWN_ERROR:
this.$vux.toast.show({
text: "",
type: 'warn'
})
break;
}
}
}
}



